import { Button, Flex, Form, Select ,message} from "antd"
import { useState,useEffect } from "react"
import axios from "axios"
import "./index.css"
const Load = ()=>{

    const [selectList, setSelectList] = useState([])
    useEffect(()=>{
        async function getList(){
        //   const list = await axios.get("http://localhost:3004/list")
          const list = await axios.get("http://81.70.210.120/load")
          setSelectList(list.data)
        }
        getList()
      },[])
    const [messageApi, contextHolder] = message.useMessage();
    const success = () => {
        messageApi.open({
        type: 'success',
        content: '',
        });
    };
    const handleSubmit = (event) => {
    //  Axios  POST 
    axios.post('/load', event)
        .then(response => {
        success()
        console.log(event)
        })
        .catch(error => {
        console.error('There was a problem with your Axios operation:', error);
        });
    };
   
    return(
        <div className="load">
            {contextHolder}
            <h2></h2>
            <Form onFinish={handleSubmit}>
                <Flex vertical = {false} gap='middle'>
                    <Form.Item label='' name='model'>
                        <Select style={{width: 130,}}
                        options={selectList.map((item)=>({
                            value:item.name,
                            label:item.name
                        }))}/>
                    </Form.Item>
                    <Form.Item>
                        <Button type="primary" htmlType="submit" ></Button>
                    </Form.Item>
                </Flex>
            </Form>
        </div>
    )
}

export default Load;