import React, {useEffect, useState} from 'react';
import axios from 'axios';
import { Flex,Select, Form,Button, message} from 'antd';

import './index.css'

const Evaluation = ()=>{
  const [messageApi, contextHolder] = message.useMessage();
  const success = () => {
    messageApi.open({
      type: 'success',
      content: '',
    });
  };

    const [data, setData] = useState('');
    const [error, setError] = useState(null);
    const [selectList, setSelectList] = useState([])
    useEffect(()=>{
        async function getList(){
          const list = await axios.get("http://81.70.210.120/evaluation")
          // const list = await axios.get("http://localhost:3004/list")
          setSelectList(list.data)
        }
        getList()
      },[])

    
    const handleSubmit = (event) => {
      //  Axios  POST 
      axios.post('/evaluation', event)
        .then(response => {
        // console.log('Data fetched successfully:', response.data);
        success()
        setData(response.data);
        })
        .catch(error => {
        console.error('There was a problem with your Axios operation:', error);
        });
      axios.get('/evaluation')
        .then(response => {
           // 
          setError(null); // 
        })
        .catch(error => {
          console.error('There was a problem with your Axios operation:', error);
          setError('There was a problem with your Axios operation'); // 
          setData(null); // 
        }); 

    };

    return(
      <div className='evaluation'>
        {contextHolder}
        <h2></h2>
        <Flex vertical >
          <Form onFinish={handleSubmit} style={{height:30}}>
            <Flex vertical = {false} gap='large'>
                <Form.Item label='' name='model' >
                  <Select style={{width: 170,}}
                  options={selectList.map((item)=>({
                      value:item.value,
                      label:item.name
                  }))}
                  />
                </Form.Item>
                <Form.Item>
                  <Button type="primary" htmlType="submit" ></Button>
                </Form.Item>
            </Flex>
          </Form>
          <div className='eva-result'>
            {/* <p></p> */}
            <p><span style={{fontWeight: 'bold'}}>F1</span>{data['F1']}</p>
            <p><span style={{fontWeight: 'bold'}}></span>{data['Accuracy']}</p>
            <p><span style={{fontWeight: 'bold'}}></span>{data['Recall']}</p>
          </div>
          {error && <div>Error: {error}</div>}
        </Flex>
       
      </div>
    )
  }

export default Evaluation