import React, {useEffect, useState} from 'react';
import axios from 'axios';
import { Form ,Input,Button,Flex,message} from 'antd';
import './index.css'

const Predict = () =>{
  const [messageApi, contextHolder] = message.useMessage();
  const success = () => {
    messageApi.open({
      type: 'success',
      content: '',
    });
  };

    //
    const [responseData, setResponseData] = useState('');
    //
    const [error, setError] = useState(null);
  
    //input

    const handleSubmit = (event) => {
      //  Axios  POST 
      axios.post('/predict', event)
        .then(response => {
          success()
          // 
          setResponseData(response.data);
          setError(null); // 
        })
        .catch(error => {
          console.error('There was a problem with your Axios operation:', error);
          setError('There was a problem with your Axios operation'); // 
          setResponseData(null); // 
        });
    };
   
    return(
      <div className='predict'>
         {contextHolder}
        <h2></h2>
        <Form onFinish={handleSubmit} style={{height:40}}> 
          <Flex vertical = {false} gap='middle'>
            <Form.Item name='str'>
              <Input size = 'large' placeholder="" style={{width:300,}}/>
            </Form.Item>
            <Form.Item>
              <Button  type="primary" htmlType="submit" size='large'>
                
              </Button>
            </Form.Item>
          </Flex>
        </Form>
        <div className='predict-result'>
          <p><span style={{fontWeight: 'bold'}}>:</span>{responseData}</p>
        </div>
      </div>
    )
  }

export default Predict