import React, {useEffect, useState} from 'react';
import axios from 'axios';
import { Button, Select, Form,Flex,InputNumber,message} from 'antd';
import './index.css'


const Training = () =>{
    //
    const [selectList, setSelectList] = useState([])
    useEffect(()=>{
      async function getList(){
        const list = await axios.get("http://81.70.210.120/training")
        // const list = await axios.get("http://localhost:3004/list")
        setSelectList(list.data)
      }
      getList()
    },[])
  
    //

    const handleSubmit = (event) => {
      //  Axios  POST 
      axios.post('/training', event)
        .then(response => {
          console.log("")
        })
        .catch(error => {
          console.error('There was a problem with your Axios operation:', error);
        });
    };
    
    //
    const [messageApi, contextHolder] = message.useMessage();
    const success1 = () => {
      messageApi.open({
        type: 'success',
        content: '',
      });
    };
    const success2 = () => {
      messageApi.open({
        type: 'success',
        content: '',
      });
    };
    return(
    <div className='training'>
      {contextHolder}
      <h2></h2>
      <Form onFinish={handleSubmit} style={{maxWidth:600}}>
        <Flex gap='large'>
          <Form.Item label='' name='model' style={{width:210}}>
            <Select style={{width: 160,}}  
              options={selectList.map((item)=>({
                value:item.name,
                label:item.name
              }))}
            >
            </Select>
          </Form.Item>
          <Form.Item label=':' name='dataset'>
            <Select style={{width: 210,}} 
              options={selectList.map((item)=>({
                value:item.dataset,
                label:item.dataset
              }))}
            >
            </Select>
          </Form.Item>
        </Flex>
        <Flex gap='large' style={{width:900}}>
          <Form.Item label='max_length' name='Parameter1' style={{width:450}}>
            <InputNumber min={0} defaultValue={0}/>
          </Form.Item> 
          <Form.Item label='per_device_train_batch_size' name='Parameter2' style={{width:700}}>
            <InputNumber min={0} defaultValue={0}/>
          </Form.Item> 
          <Form.Item label='learning_rate' name='Parameter3' style={{width:900}}>
            <InputNumber min={0} defaultValue={0}/>
          </Form.Item> 
        </Flex>
        <Flex gap='large' vertical = {false}>
          <Form.Item>
            <Button type="primary" htmlType="submit" onClick={success1}>
              
            </Button></Form.Item>
            <Form.Item>
            <Button type="primary" htmlType="submit" onClick={success2}>
              
            </Button>
          </Form.Item>
        </Flex>
      </Form>
      {/* <form method='POST'  onSubmit={handleSubmit}>
        <label>model:</label>
        <select value={selectedOption} onChange={handleChange} name='model'> 
          {selectList.map(item=><option id={item.id}>{item.name}</option>)}
        </select>
        <label>dataset:</label>
        <select value={selectedOption} onChange={handleChange} name='dataset'>
          {selectList.map(item=><option id={item.id}>{item.dataset}</option>)}
        </select>
        
      </form>
      <Button type='submit'>Train</Button> */}
    </div>
  
    )
  }

export default Training